        getUserPublicInfo: function (context) {
            return new Promise(function (resolve, reject) {
                let userID = firebase.auth().currentUser.uid;

                context.dispatch('getUserPublicInfoDocByUIDFirebase', userID).then(function (userDoc) {
                    if (userDoc.exists) {
                        context.commit('setUserInfoUsername', userDoc.data().username);
                        context.commit('setUserInfoBirthDate', userDoc.data().birthDate);
                        context.commit('setUserInfoGender', userDoc.data().gender);
                        context.commit('setUserInfoDescription', userDoc.data().description);
                        context.commit('setUserInfoKnownLanguages', userDoc.data().knownLanguages);
                        context.commit('setUserInfoLanguagesToLearn', userDoc.data().languagesToLearn);
                        context.commit('setUserInfoProfileImage', userDoc.data().profileImage);
                        context.commit('setUserInfoAvgRating', userDoc.data().avgRating);
                        context.commit('setUserInfoNumRatings', userDoc.data().numRatings);

                        context.dispatch('createChatIDsListenerFirebase')
                            .then(() => {
                                let userChats = userDoc.data().chats;

                                try {
                                    let promises = [];
                                    userChats.forEach(chatID => {
                                        promises.push(context.dispatch('createChatListenerFirebase', chatID));
                                    });
                                    Promise.all(promises).then(function () {
                                        resolve();
                                    })
                                        .catch(function (error) {
                                            reject(error);
                                        });
                                } catch (e) {
                                    console.error(e);
                                    reject(e);
                                }
                            })
                            .catch((error) => {
                                console.error(error);
                                reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                            });
                    } else {
                        console.warn("No such document!");
                        reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                    }
                }).catch(function (error) {
                    console.warn("Error getting document:", error);
                    reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                });
            });
        },
