        setChatWithUserMessagesToRead: function (context, chatID) {
            let firestoreDB = firebase.firestore();

            return new Promise(function (resolve, reject) {
                let chatIndex = context.state.chats.findIndex(chat => {
                    return chat.id == chatID;
                });

                let promises = [];

                context.state.chats[chatIndex].messages.forEach(message => {
                    if (!message.read && message.sender != context.state.userInfo.uid) {
                        promises.push(firestoreDB.collection("Chats").doc(chatID).collection("Messages").doc(message.id).update(
                            {
                                'read': true
                            })
                            .then(() => {
                                console.log("Message successfully updated read state ");
                                /*let setMessageToReadData =
                                    {
                                        'chatID': chatID,
                                        'messageID': message.id
                                    };
                                context.commit('setMessageToRead', setMessageToReadData);*/
                            })
                            .catch(function (error) {
                                console.error("Error updating message read state: ", error);
                                reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                            }));
                    }
                });

                Promise.all(promises).then(function () {
                    resolve();
                })
                    .catch(function () {
                        console.error("Error updating messages read state: ");
                        reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                    });
            });
        },
        sendChatMessageFirebase: function (context, data) {
            let firestoreDB = firebase.firestore();

            return new Promise(function (resolve, reject) {
                let chatIndex = context.state.chats.findIndex(chat => {
                    return chat.id == data.chatID;
                });


                let messageIndex = context.state.chats[chatIndex].messages.length;


                let newMessage = {
                    'created': firebase.firestore.FieldValue.serverTimestamp(),
                    'text': data.messageText,
                    'read': false,
                    'sender': context.state.userInfo.uid,
                    /*'id': messageIndex.toString() + context.state.userInfo.uid,*/
                };

                let promises = [];
                if (messageIndex == 0) {
                    let chatID;
                    let myUID = context.state.userInfo.uid;
                    let otherUserUID = context.getters.getOtherUserInChatByChatID(data.chatID);

                    if (myUID < otherUserUID) {
                        chatID = myUID + otherUserUID;
                    } else {
                        chatID = otherUserUID + myUID;
                    }

                    promises.push(firestoreDB.collection("UsersPublicInfo").doc(myUID).update(
                        {
                            'chats': firebase.firestore.FieldValue.arrayUnion(chatID),
                        }
                    )
                        .then(() => {
                            context.commit("addChatIDToChatIDs", chatID);
                            console.log("chatID successfuly written p1")
                        })
                        .catch(function (error) {
                            console.error("Error writing chatID", error);
                            reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                        }));

                    promises.push(firestoreDB.collection("UsersPublicInfo").doc(otherUserUID).update(
                        {
                            'chats': firebase.firestore.FieldValue.arrayUnion(chatID),
                        }
                    ).then(() => {
                        console.log("chatID successfuly written p2")
                    })
                        .catch(function (error) {
                            console.error("Error writing chatID", error);
                            reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                        }));
                }

                Promise.all(promises).then(function () {

                    firestoreDB.collection("Chats").doc(data.chatID).collection("Messages").doc().set(newMessage)
                        .then(() => {
                            console.log("Message successfully added");
                            resolve();
                        })
                        .catch(function (error) {
                            console.error("Error adding message", error);
                            reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                        });
                })
                    .catch(function (error) {
                        console.error("Error writing chatID on users chatIDs: " + error);
                        reject(Constants.ERROR_GENERIC_CONNECTION_PROBLEM);
                    });


            });
        }
