        setRatingInRatingsFirebase: function (context, data) {
            return new Promise(function (resolve, reject) {
                let firestoreDB = firebase.firestore();
                let newRating = {};

                newRating[data.to] = data.rating;
                if (data.rating) {
                    firestoreDB.collection("Ratings").doc(data.from).set(newRating, {merge: true})
                        .then(() => {
                            console.log("Rating successfully written in Ratings");
                            resolve();
                        })
                        .catch(function (error) {
                            console.error("Error writing rating info in Ratings: ", error);
                            reject(error);
                        });
                } else {
                    firestoreDB.collection("Ratings").doc(data.from).update({
                        [data.to]: firebase.firestore.FieldValue.delete()
                    })
                        .then(() => {
                            console.log("Rating successfully deleted of Ratings");
                            resolve();
                        })
                        .catch(function (error) {
                            console.error("Error deleting rating info of Ratings: ", error);
                            reject(error);
                        });
                }

            });
        },
